import React from 'react';
import { Link } from 'react-router-dom';

const Menu = () => {
    return(
            <nav class="navbar navbar-expand-lg navbar-light navigation border-style">
                    <button class="navbar-toggler icon-bg mx-auto " type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span class="navbar-toggler-icon"></span>
                    </button>
                        <div class="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul class="navbar-nav ml-auto mr-auto pl-5 pr-5 h5 ">
                                    <li class="nav-item active">
                                        <Link class="nav-link text-white" to="/">Search By Countries</Link>
                                    </li>
                                    <li class="nav-item ">
                                        <Link class="nav-link text-white" to="/menuComp/indiaState">Search By Indian State</Link>
                                    </li>
                                    <li class="nav-item">
                                        <Link class="nav-link text-white" to="/menuComp/total">Total Cases(World)</Link>
                                    </li>
                            </ul>
                        </div>        
            </nav>
    );
};

export default Menu;