import React from 'react';
import SearchIndia from '../component/searchIndia';
import ResultIndia from '../component/resultIndia';
import covid19indiaapi from '../api/covid19indiaapi';

class Countries extends React.Component{
    state = {result: [],
        name:""   
       };

            onSearchSubmit = async (term) => {
            try{
                const response = await covid19indiaapi.get(); 
            if(response){
                const states = response.data.data.statewise;
                states.forEach( indianstate => {
                    if(indianstate.state.toLowerCase() === term.toLowerCase()){
                        console.log(indianstate);
                        this.setState({result: indianstate});
                    }
                })
                this.setState({name: term});
            } else {
                alert("Check Country Name");
            }
            } catch {
                alert("Failed to fetch Data")
            }
            }
    
    render(){
        return(
            <div>
                <div className="search">
                    <SearchIndia onSubmit ={this.onSearchSubmit}/>
                </div>
                <div className="Result">
                    <ResultIndia name={this.state.name} data={this.state.result} />
                </div>
            </div>
        );
    }
}

export default Countries;

