import React from 'react';
import Header from './Header.js';
import Footer from './Footer.js';
import Menu from './Menu.js';
import { BrowserRouter, Route } from 'react-router-dom';
import Countries from '../menuComp/countries.js';
import Total from '../menuComp/total';
import IndiaState from '../menuComp/indiaState';




class App extends React.Component{
    render(){
        return (
            <div className="main-cont">
                    <div className="header">
                        <Header />
                    </div>
                        <BrowserRouter>
                            <div className="menu">
                                <Menu />
                                <Route path="/" exact component={Countries}></Route>
                                <Route path="/menuComp/total" exact component={Total}></Route>
                                <Route path="/menuComp/indiaState" exact component={IndiaState}></Route>
                            </div>
                        </BrowserRouter>
                    <div className="footer">
                        <Footer />
                    </div>
            </div>
            );
    }

};

export default App;
