import React from 'react';

const Result = (props) => {
    return (
       <div className="container mt-5 mb-5 text-center">
            <h1><span className="country-name">{props.name.toUpperCase()}</span></h1>

            <div className="d-flex container justify-content-center">
                <div className="col-8">
                    <ul class="list-group shadow-box text-center h4">
                        <li class="list-group-item result-bg">Confirmed: {props.data.confirmed}</li>
                        <li class="list-group-item result-bg">Recovered: {props.data.recovered}</li>
                        <li class="list-group-item result-bg">Active: {props.data.active}</li>
                        <li class="list-group-item result-bg">Decieved: {props.data.deaths}</li>
                    </ul>
                </div>
            </div>

       </div>
    );
};

export default Result;