import React from 'react';

class Search extends React.Component{
    
    state = { term : ""} 

    onFormSubmit = (e) => {
        e.preventDefault();
        this.props.onSubmit(this.state.term);
    }

    render() {
        return (
            <div className="container-fluid search">
                <form onSubmit={this.onFormSubmit}>
                    <div class=" rounded-lg p-4 form-group row justify-content-center text-white">
                            <div class="col-sm-4 mt-1">
                                <input type="text" class="form-control input-bg" value= {this.state.value} onChange={(e) => this.setState({term: e.target.value})} placeholder="Enter A Country" />
                            </div>
                            <button type="submit" class="btn button-bg">Search</button>  
                    </div>
                </form>
            </div>
        );
    }
};

export default Search;


